:root {
  --primary: #00f0ff;
  --secondary: #ff00ff;
  --accent: #ffff00;
  --dark: #0a0a0a;
  --dark-100: #1a1a1a;
  --dark-200: #2a2a2a;
}
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}
html {
  scroll-behavior: smooth;
}
body {
  background-color: var(--dark);
  color: white;
  font-family: 'Poppins', 'Inter', system-ui, sans-serif;
  overflow-x: hidden;
}
[dir="rtl"] {
  text-align: right;
}
[dir="rtl"] .text-left {
  text-align: right;
}
[dir="rtl"] .text-right {
  text-align: left;
}
.neon-text {
  text-shadow: 0 0 5px var(--primary),
    0 0 10px var(--primary),
    0 0 20px var(--primary),
    0 0 40px var(--primary);
}
.neon-text-magenta {
  text-shadow: 0 0 5px var(--secondary),
    0 0 10px var(--secondary),
    0 0 20px var(--secondary);
}
.neon-border {
  box-shadow: 0 0 5px var(--primary),
    0 0 10px var(--primary),
    inset 0 0 5px rgba(0, 240, 255, 0.1);
  border: 1px solid var(--primary);
}
.neon-border-magenta {
  box-shadow: 0 0 5px var(--secondary),
    0 0 10px var(--secondary),
    inset 0 0 5px rgba(255, 0, 255, 0.1);
  border: 1px solid var(--secondary);
}
.glow-button {
  position: relative;
  background: linear-gradient(135deg, var(--primary), var(--secondary));
  border: none;
  padding: 12px 32px;
  border-radius: 50px;
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 1px;
  cursor: pointer;
  transition: all 0.3s ease;
  overflow: hidden;
}
.glow-button::before {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(
    90deg,
    transparent,
    rgba(255, 255, 255, 0.3),
    transparent
  );
  transition: left 0.5s ease;
}
.glow-button:hover::before {
  left: 100%;
}
.glow-button:hover {
  transform: translateY(-2px);
  box-shadow: 0 0 20px var(--primary),
    0 0 40px var(--secondary);
}
.gradient-text {
  background: linear-gradient(135deg, var(--primary), var(--secondary));
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}
.game-card {
  position: relative;
  overflow: hidden;
  border-radius: 16px;
  transition: all 0.4s cubic-bezier(0.175, 0.885, 0.32, 1.275);
}
.game-card::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: linear-gradient(
    180deg,
    transparent 0%,
    rgba(0, 0, 0, 0.8) 100%
  );
  z-index: 1;
  opacity: 0.7;
  transition: opacity 0.3s ease;
}
.game-card:hover::before {
  opacity: 0.9;
}
.game-card:hover {
  transform: translateY(-10px);
  box-shadow: 0 20px 40px rgba(0, 240, 255, 0.2),
    0 0 20px rgba(255, 0, 255, 0.1);
}
.video-container {
  position: relative;
  width: 100%;
  height: 100%;
  overflow: hidden;
}
.video-container video {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
::-webkit-scrollbar {
  width: 8px;
}
::-webkit-scrollbar-track {
  background: var(--dark-100);
}
::-webkit-scrollbar-thumb {
  background: linear-gradient(var(--primary), var(--secondary));
  border-radius: 4px;
}
::-webkit-scrollbar-thumb:hover {
  background: var(--primary);
}
@keyframes float {
  0%, 100% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(-20px);
  }
}
@keyframes pulse-glow {
  0%, 100% {
    box-shadow: 0 0 20px var(--primary);
  }
  50% {
    box-shadow: 0 0 40px var(--primary), 0 0 60px var(--secondary);
  }
}
@keyframes scan-line {
  0% {
    transform: translateY(-100%);
  }
  100% {
    transform: translateY(100vh);
  }
}
.animate-float {
  animation: float 3s ease-in-out infinite;
}
.animate-pulse-glow {
  animation: pulse-glow 2s ease-in-out infinite;
}
.section-title {
  font-family: 'Orbitron', sans-serif;
  font-weight: 700;
  font-size: 2.5rem;
  margin-bottom: 1rem;
  color: #FFFFFF;
  text-shadow: 0 0 15px rgba(255, 255, 255, 0.9), 0 0 40px rgba(8, 247, 254, 0.55), 0 0 60px rgba(255, 0, 229, 0.3);
}
@media (min-width: 768px) {
  .section-title {
    font-size: 3.5rem;
  }
}
.form-input {
  background: var(--dark-100);
  border: 1px solid var(--dark-200);
  border-radius: 8px;
  padding: 12px 16px;
  color: white;
  width: 100%;
  transition: all 0.3s ease;
}
.form-input:focus {
  outline: none;
  border-color: var(--primary);
  box-shadow: 0 0 10px rgba(0, 240, 255, 0.3);
}
.form-input::placeholder {
  color: #666;
}
.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.9);
  backdrop-filter: blur(10px);
  z-index: 100;
  display: flex;
  align-items: center;
  justify-content: center;
}
.modal-content {
  background: var(--dark-100);
  border: 1px solid var(--primary);
  border-radius: 20px;
  max-width: 800px;
  width: 90%;
  max-height: 90vh;
  overflow-y: auto;
  position: relative;
}
.whatsapp-float {
  position: fixed;
  bottom: 30px;
  right: 30px;
  width: 60px;
  height: 60px;
  background: #25d366;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  box-shadow: 0 4px 20px rgba(37, 211, 102, 0.4);
  z-index: 99;
  transition: all 0.3s ease;
}
.whatsapp-float:hover {
  transform: scale(1.1);
  box-shadow: 0 6px 30px rgba(37, 211, 102, 0.6);
}
[dir="rtl"] .whatsapp-float {
  right: auto;
  left: 30px;
}
